<template>
  <div class="main">
    <div class="header">大富翁随机数生成器</div>

    <!-- <div>{{current}}</div> -->
    <div class="imgs">
      <canvas id="canvas" class="disk" width="350" height="350">抱歉！浏览器不支持。</canvas>

      <!-- <img src="../assets/imgs/disk.png" class="disk" alt /> -->
      <img src="../assets/imgs/pointer.png" @click="getNum" alt class="pointer" />
    </div>

    <!-- <button @click="getNum">点击</button> -->
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      current: 1,
      deg: 10
    };
  },
  methods: {
    getNum() {
      // 获取 1-12 之间的随机数
      this.current = Math.floor(Math.random() * 7) + 1;
      const box = $(".disk");
      // 获取随机旋转度数
      let deg = Math.floor(Math.random() * 360);
      box.css({
        transform: `rotate(${360 * 5 + deg}deg)`,
        transition: "5s cubic-bezier(0,0,0.001,1)"
      });
      // 重设样式参数
      setTimeout(function() {
        box.css({ transform: `rotate(${deg}deg)`, transition: "" });
      }, 5000);
    },
    // 绘制转盘
    getDisk() {
      var canvas = document.getElementById("canvas");
      if (!canvas.getContext) {
        alert("抱歉！浏览器不支持。");
        return;
      }
      // 获取绘图上下文
      var ctx = canvas.getContext("2d");
      let num = 12;
      for (var i = 0; i < num; i++) {
        // 保存当前状态
        ctx.save();
        // 开始一条新路径
        ctx.beginPath();
        // 位移到圆心，下面需要围绕圆心旋转
        ctx.translate(175, 175);
        // 从(0, 0)坐标开始定义一条新的子路径
        ctx.moveTo(0, 0);
        // 旋转弧度,需将角度转换为弧度,使用 degrees * Math.PI/180 公式进行计算。
        ctx.rotate((((360 / num) * i + 360 / num / 2) * Math.PI) / 180);
        // 绘制圆弧
        ctx.arc(0, 0, 175, 0, (2 * Math.PI) / num, false);
        if (i % 2 == 0) {
          ctx.fillStyle = "#ffb820";
        } else {
          ctx.fillStyle = "#ffcb3f";
        }
        // 填充扇形
        ctx.fill();
        // 绘制边框
        ctx.lineWidth = 0.5;
        ctx.strokeStyle = "#f48d24";
        ctx.stroke();

        // 文字
        ctx.fillStyle = "#fff";
        ctx.font = "24px sans-serif";
        ctx.fillText(i + 1, 100, 60);
        // 恢复前一个状态
        ctx.restore();
      }
    }
  },
  mounted() {
    this.getDisk();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  font-size: 40px;
}
.main .imgs {
  width: 375px;
  height: 375px;
  overflow: hidden;
  text-align: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main .imgs .disk {
  width: 350px;
  height: 350px;
}

.main .imgs .pointer {
  width: 87px;
  height: 114px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

button {
  background-color: skyblue;
  width: 25%;
  border: 2px solid #fff;
  box-shadow: 2px 2px 8px #ccc;
  border-radius: 5%;
  font-size: 24px;
  color: white;
  padding: 5px 10px;
}
</style>



